.breadcrumb {
  background-color: #F9FDFF;
}
.breadcrumb li a {
  color: black;
  text-decoration: none !important;
}
.breadcrumb li a:hover {
  color: #6DDA5F;
}
.breadcrumb-item + .breadcrumb-item::before {
  content: ">" !important;
  color: #6DDA5F !important;
}

.fab-container {
  display: flex;
  position: fixed;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  user-select: none;
  bottom: 30px;
  right: 30px;
}
.fab-container:hover {
  height: 100%;
}
.fab-container:hover .sub-button:nth-child(2) {
  transform: translateY(-80px);
}
.fab-container:hover .sub-button:nth-child(3) {
  transform: translateY(-140px);
}
.fab-container:hover .sub-button:nth-child(4) {
  transform: translateY(-200px);
}
.fab-container:hover .sub-button:nth-child(5) {
  transform: translateY(-260px);
}
.fab-container:hover .sub-button:nth-child(6) {
  transform: translateY(-320px);
}
.fab-container .fab {
  position: relative;
  height: 70px;
  width: 70px;
  background-color: #6DDA5F;
  border-radius: 50%;
  z-index: 2;
}
.fab-container .fab::before {
  content: " ";
  position: absolute;
  bottom: 0;
  right: 0;
  height: 35px;
  width: 35px;
  background-color: inherit;
  border-radius: 0 0 10px 0;
  z-index: -1;
}
.fab-container .fab .fab-content {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  border-radius: 50%;
}
.fab-container .fab .fab-content .material-icons {
  color: white;
  font-size: 40px;
}
.fab-container .sub-button {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 10px;
  right: 10px;
  height: 50px;
  width: 50px;
  background-color: #6DDA5F;
  border-radius: 50%;
  transition: all 0.3s ease;
}
.fab-container .sub-button:hover {
  cursor: pointer;
}
.fab-container .sub-button .material-icons {
  font-family: "Font Awesome 5 Brands";
  color: white;
  padding-top: 6px;
}

.section-author {
  margin-top: 54px;
  padding: 10px 50px;
}
.section-author__profile-container {
  position: relative;
  max-width: 133px;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
}
.section-author__profile-container::before {
  content: "";
  display: block;
  padding-top: 100%;
}
.section-author__profile-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}